:root {
  --primary: #feffff;
  --text: #3f3d56;
  --highlight: #3aafa9;
}

* {
  box-sizing: border-box;
  color: var(--text);
  margin: 0;
  padding: 0;
  font-family: "Baloo Bhai 2", sans-serif;
}

html {
  scroll-behavior: smooth;
}

.container, .container-alt {
  width: 100%;
}

.container-alt {
  background-color: var(--highlight);
}

section {
  max-width: 1250px;
  margin: 0 auto;
  padding: 1em 0 0;
}

.header {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header-top {
  justify-content: space-between;
  width: 100%;
  margin: 2.5em 0;
  display: inline-flex;
}

.header-top .logo {
  width: auto;
  max-width: 50px;
  height: auto;
  max-height: 50px;
  display: block;
}

.header-links {
  justify-content: center;
  align-items: center;
  gap: 1em;
  display: flex;
}

a {
  padding: .75em !important;
}

.header-links a:last-of-type {
  text-decoration: none;
  padding: 1.25em 2em !important;
}

i {
  color: var(--text);
}

button, a {
  cursor: pointer;
  border: 2px solid var(--highlight);
  background-color: var(--primary);
  color: var(--text);
  border-radius: 999px;
  padding: 1.25em 2em;
  font-size: 1rem;
  font-weight: 600;
  line-height: 0;
  transition: all .25s;
}

a:hover i {
  color: var(--primary);
}

button:hover, a:hover {
  background-color: var(--highlight);
  color: var(--primary);
}

.header h1 {
  color: var(--text);
  align-self: flex-start;
  font-size: 5rem;
  line-height: 75px;
}

.header h1 span {
  color: var(--highlight);
}

h1 {
  font-size: 2em;
}

.animated-text {
  align-self: flex-start;
  margin-bottom: .5em;
  font-size: 2.5rem;
  font-weight: 800;
}

.animated-text span {
  margin: 0 auto;
  position: relative;
}

.animated-text span:before {
  content: "Software Engineer";
  color: var(--highlight);
  animation: 20s .5s infinite words;
}

.animated-text span:after {
  content: "";
  background-color: var(--primary);
  border-left: 5px solid var(--highlight);
  width: calc(100% + 8px);
  height: 100%;
  animation-name: cursor, typing;
  animation-duration: 2s, 20s;
  animation-timing-function: ease, steps(10, end);
  animation-iteration-count: infinite, infinite;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: .5s;
  animation-fill-mode: none, none;
  position: absolute;
  right: -8px;
}

@keyframes cursor {
  to {
    border-left: 5px solid #3aafa937;
  }
}

@keyframes words {
  0%, 20% {
    content: "Software Engineer";
  }

  21%, 40% {
    content: "Designer";
  }

  41%, 60% {
    content: "Student";
  }

  61%, 80% {
    content: "Back-end Developer";
  }

  81%, 100% {
    content: "Front-end Developer";
  }
}

@keyframes typing {
  10%, 15%, 30%, 35%, 50%, 55%, 70%, 75%, 90%, 95% {
    width: 0;
  }

  5%, 20%, 25%, 40%, 45%, 60%, 65%, 80%, 85% {
    width: calc(100% + 8px);
  }
}

.header .graphic {
  width: 100%;
  min-width: 200px;
  max-width: 750px;
  margin: 3em auto 0;
}

.header p {
  align-self: flex-start;
  max-width: 75ch;
  font-size: 1.25rem;
}

.parallax {
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 200px;
  max-width: 750px;
  height: 80vw;
  max-height: 550px;
  margin: 3em auto 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.parallax img {
  pointer-events: none;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.reveal {
  opacity: 0;
  transition: all 1s;
  position: relative;
  transform: translateY(75px);
}

.reveal.active {
  opacity: 1;
  transform: translateY(0);
}

.about-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: fit-content;
  padding: 5em 0 2.5em;
  display: flex;
}

.about {
  grid-template-columns: 1fr auto;
  gap: 5em;
  margin: 1em auto;
  display: grid;
}

.about-content {
  margin: auto 0;
}

.about h1 {
  align-self: center;
}

.about p {
  color: var(--text);
  font-size: 1.1rem;
  font-weight: 300;
}

.canvas {
  width: 500px;
  height: 500px;
  transition-delay: 5s;
  position: relative;
}

.canvas img {
  position: absolute;
}

.uni-logo {
  width: 150px;
  animation: 5s ease-in-out infinite rotateAndTranslate3;
  top: 5px;
  left: 25px;
}

.mri-logo {
  width: 200px;
  animation: 5s ease-in-out 1.5s infinite rotateAndTranslate2;
  bottom: 75px;
  right: 50px;
}

.canvas h1 {
  font-size: 10rem;
  position: absolute;
  bottom: 30px;
  left: 75px;
}

.comp {
  width: 300px;
  animation: 5s ease-in-out 1s infinite rotateAndTranslate;
  top: -15px;
  right: 20px;
}

.degree {
  width: 200px;
  animation: 5s ease-in-out .5s infinite rotateAndTranslate2;
  top: 200px;
  left: 35px;
}

.projects-section {
  text-align: center;
  flex-direction: column;
  gap: 2em;
  padding: 5em 0 8em;
  display: flex;
}

.card-container {
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1em;
  display: grid;
}

.project-card {
  text-align: start;
  border: 1px solid #d3d3d3;
  border-radius: 20px;
  flex-direction: column;
  padding: 2em 2em 1em;
  display: flex;
}

.project-card h2 {
  margin-bottom: .5em;
  font-weight: 600;
  line-height: 30px;
}

.project-card h4 {
  font-weight: 500;
}

.project-card img {
  object-fit: contain;
  height: 200px;
}

.project-card .tags-container {
  flex-wrap: wrap;
  gap: .4em;
  margin-bottom: .5em;
  display: flex;
}

.project-card span {
  color: #fff;
  background-color: var(--highlight);
  border-radius: 999px;
  padding: .1em 1em;
  font-weight: 500;
}

.project-card a {
  border: none;
  align-self: flex-end;
  width: fit-content;
  margin-top: auto;
}

.project-card a i {
  font-size: 2em;
}

.contact-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5em;
  padding-bottom: 2em;
  display: flex;
}

.contact-box {
  background-color: var(--text);
  text-align: center;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 2em;
  width: 80%;
  height: 150px;
  margin-top: -5em;
  display: flex;
}

.contact-box h1, .contact-box i {
  color: var(--primary);
}

.contact-box i {
  font-size: 1.5rem;
}

.contact-box a {
  background-color: var(--text);
  color: var(--primary);
  text-decoration: none;
  padding: 1em 2em !important;
}

.email-link:hover {
  background-color: var(--highlight);
}

.contact-section .logo-alt {
  filter: brightness(0) invert();
  object-fit: contain;
  height: 75px;
  margin: 2.5em;
}

.footer-links {
  gap: 1em;
  display: inline-flex;
}

.footer-links a {
  border: 2px solid var(--primary);
  background-color: var(--highlight);
}

.footer-links a i {
  color: var(--primary);
}

.footer-links a:hover {
  background-color: var(--primary);
}

.footer-links a:hover i {
  color: var(--highlight);
}

.contact-section h2 {
  margin-top: 2em;
  font-size: 1.25rem;
  font-weight: 500;
}

@keyframes rotateAndTranslate {
  0% {
    transform: translateY(0)rotate(0);
  }

  50% {
    transform: translateY(10px)scale(1.01)rotate(5deg);
  }

  100% {
    transform: translateY(0)rotate(0);
  }
}

@keyframes rotateAndTranslate2 {
  0% {
    transform: translateY(0)rotate(-5deg);
  }

  50% {
    transform: translateY(-10px)scale(1.01)rotate(0);
  }

  100% {
    transform: translateY(0)rotate(-5deg);
  }
}

@keyframes rotateAndTranslate3 {
  0% {
    transform: translateY(10px)rotate(-20deg);
  }

  50% {
    transform: translateX(0)scale(1.03)rotate(-10deg);
  }

  100% {
    transform: translateY(10px)rotate(-20deg);
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media (width <= 1550px) {
  section {
    max-width: 100vw;
    margin: 0;
    padding: 5em 2em !important;
  }

  .header h1 {
    line-height: 75px;
  }

  .about {
    flex-direction: column;
    gap: 0;
    display: flex;
  }

  .canvas {
    margin: 0 auto;
  }

  .contact-box {
    margin-top: -7em;
  }
}

@media (width <= 1000px) {
  .contact-box {
    flex-direction: column;
    height: fit-content;
    padding: 2em;
  }
}

@media (width <= 575px) {
  .canvas, .animated-text {
    display: none;
  }

  .header h1 {
    font-size: 3rem;
    line-height: 50px;
  }

  .project-card {
    justify-self: center;
    max-width: 80%;
  }
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: var(--text);
}

::-webkit-scrollbar-thumb {
  background-color: var(--highlight);
  border: 2px solid var(--text);
  border-radius: 999px;
}
